<template>
  <PromptModal :width="400" title="创建同级文件夹" :visible="promptVisible" @handleOk="handleCreateFolder"
               @changeVisible="(visible)=>this.promptVisible=visible">
    <template #message>
      请输入同级文件夹的名称， 此文件夹将会创建到此文件夹下
      <blockquote class="pathBlock">
        {{ this.selectedDir }}
      </blockquote>
    </template>
  </PromptModal>
  <PromptModal message="请输入子文件夹名称" title="创建子文件夹"
               :visible="promptVisibleCreateChild"
               @handleOk="handleCreateSubFolder()"
               @changeVisible="(visible)=>this.promptVisibleCreateChild=visible"
  >
    <template #message>
      请输入同级文件夹的名称， 此文件夹将会创建到此文件夹下
      <blockquote class="pathBlock">
        {{ this.selectedDir }}
      </blockquote>
    </template>
  </PromptModal>
  <PromptModal :width="400" title="重命名文件夹" :defaultValue="renameFolderName" :visible="promptRenameVisible" @handleOk="handleRenameFolder"
               @changeVisible="(visible)=>this.promptRenameVisible=visible">
    <template #message>
      您正在重命名以下文件夹的名称
      <blockquote class="pathBlock">
        {{ this.selectedDir }}
      </blockquote>
    </template>
  </PromptModal>
  <div class="tree-root" :class="{'active-root':this.selectedKeys[0]===''}" style="font-size: 13px;margin-top: 5px;cursor:pointer;" @click="select([''])"><HomeOutlined /> 个人收藏</div>
  <a-empty description="" :image="simpleImage" v-if="treeData.length===0"/>
  <a-directory-tree :selectedKeys="selectedKeys" ref="tree" v-else class="tree"
                    :field-names="fields"
                    :tree-data="treeData"
                    @select="select"
  >
    <template #title="{ path, parentDir,relativePath,title,contentCount ,dirname}">
      <a-dropdown :trigger="['contextmenu']" >
        <span >{{ title }} <span v-if="Number(contentCount>0)">({{contentCount}})</span></span>
        <template #overlay>
          <a-menu>
            <a-menu-item key="create" @click="createFolder(parentDir)">创建文件夹（同级）</a-menu-item>
            <a-menu-item key="createChild" @click="createSubFolder(relativePath)">创建子文件夹</a-menu-item>
<!--            <a-menu-item key="move">移动文件夹</a-menu-item>-->
            <a-menu-item @click="openDir(relativePath)" key="locate">定位</a-menu-item>
            <a-menu-divider></a-menu-divider>
            <a-menu-item @click="renameFolder(relativePath,dirname)" key="rename">重命名</a-menu-item>
            <a-menu-item @click="removeDir(path)" key="remove">删除文件夹</a-menu-item>
            <a-menu-divider></a-menu-divider>
            <a-menu-item @click="refresh" key="locate">刷新列表</a-menu-item>
          </a-menu>
        </template>
      </a-dropdown>
    </template>
  </a-directory-tree>
</template>

<script>
// // import { defineComponent, ref, watch } from 'vue';
// import { Empty } from 'ant-design-vue';

import PromptModal from "@/components/util/PromptModal";
import ipc from "@/utils/ipc";
import {Modal} from "ant-design-vue";
import Empty from "ant-design-vue/es/empty/empty";
import {HomeOutlined} from '@ant-design/icons-vue'
export default {
  name: 'TreeList',
  props: ['treeData', 'fields'],
  components: {
    PromptModal,
    HomeOutlined
  },
  emit:['selected'],
  data() {
    return {
      promptVisible: false,
      promptVisibleCreateChild: false,
      promptRenameVisible:false,
      selectedDir: '',
      renameFolderName:'',
      simpleImage:Empty.PRESENTED_IMAGE_SIMPLE,
      selectedKeys:[]
    }
  },
  methods: {
    /**
     * 手动刷新列表
     */
    refresh(){
      window.app.$store.dispatch('subFolder/updateTreeData')
    },
    /**
     * 打开目录
     * @param relativePath
     */
    openDir(relativePath){
      ipc.openDir(this.$store.state.config.storePath+relativePath)
    },
    select(selectedKeys, e){
      if(selectedKeys[0]===''){
        this.selectedKeys=[]
      }
      this.selectedKeys=selectedKeys
      this.$emit('onSelect',selectedKeys,e)
    },

    /**
     * 创建文件夹，弹出创建文件夹弹窗
     * @param parentDir
     */
    createFolder(parentDir) {
      this.selectedDir=parentDir
      this.promptVisible = true
    },
    /**
     * 处理创建文件夹
     * @param arg
     */
    handleCreateFolder(arg) { //创建文件夹
      ipc.createDir(this.$store.state.config.storePath + this.selectedDir, arg.value, this.$message)
      arg.input.setValue('')
      this.promptVisible = false
      //this.promptVisible=true
    },

    /**
     * 重命名，弹出重命名文件夹弹窗
     * @param relativePath
     * @param dirname
     */
    renameFolder(relativePath,dirname){
      this.selectedDir='/'+relativePath
      this.renameFolderName=dirname
      this.promptRenameVisible=true
    },
    /**
     * 处理重命名文件夹方法
     * @param arg
     */
    handleRenameFolder(arg) { //重命名文件夹处理方法
      ipc.renameFolder(this.selectedDir,arg.input.input.value)
      arg.input.setValue('')
      this.promptRenameVisible = false
    },

    /**
     * 创建子文件夹弹窗
     * @param relativePath
     */
    createSubFolder(relativePath) {
      this.selectedDir='/'+relativePath
      this.promptVisible = true
    },
    /**
     * 处理创建子文件夹
     * @param arg
     */
    handleCreateSubFolder(arg){//创建子文件夹
      ipc.createDir(this.$store.state.config.storePath + this.selectedDir, arg.value, this.$message)
      arg.input.setValue('')
      this.promptVisible = false
    },


    /**
     * 删除确认
     * @param path
     */
    removeDir(path){
      Modal.confirm({
        title: '删除确认',
        content: '是否确认删除文件夹？此操作将把本地文件夹移入计算机的回收站。',
        okText: '确认',
        cancelText: '取消',
        onOk:()=>{
          ipc.trashItem(path)
          setTimeout(function (){
            window.app.$store.dispatch('subFolder/updateTreeData')
          },1000)
        }
      });
    }
  }
}


// export default defineComponent({
