//       console.log('selectedKeys', selectedKeys);
//     });
//     return {
//       expandedKeys,
//       selectedKeys,
//       simpleImage:Empty.PRESENTED_IMAGE_SIMPLE
//     };
//   },
//
// });
</script>
<style scoped>
.pathBlock {
  padding: 5px;
  padding-left: 10px;
  background: #f1f1f1;
  color: #666;
  margin-top: 5px;
  font-size: 12px
}
</style>
