<template>
  <div v-if="subFolders.length>0">
    <h3 style="margin-bottom:10px;">
      <span style="cursor: pointer" @click="toggleShowFolderList">文件夹（{{ subFolders.length }}）
      <caret-down-outlined v-show="showFolderList"/>
        <caret-right-outlined v-show="!showFolderList"/>
      </span>
    </h3>
    <div v-show="showFolderList" style="min-height:150px">
      <a-dropdown :trigger="['contextmenu']" v-for="folder in subFolders" v-bind:key="folder" >
        <div  class="sub-folder-wrapper" @click="selectItem(folder)" @dblclick="changePath(folder.name)"
             :style="{width:itemSize+'px',height:itemSize+50+'px'}" style="">
          <div :class="{hover:folder.hover}" @dragenter="onDragEnter($event,folder)"
               @dragleave="onDragLeave($event,folder)"
               @dragover="onDragOver($event,folder)"
               @drop="onDrop($event,folder)" class="cover-wrapper" style="position: relative"
               :style="{width:itemSize+'px',height:itemSize+'px'}">
            <div class="sub-folder-frame" :style="{width:itemSize+'px',height:itemSize+'px'}"></div>
            <div v-if="folder.meta.new" class="type-label new-label">New</div>
            <a-image style="" :style="{width:itemSize+'px',height:itemSize+'px'}" :preview="false"
                     class="sub-folder-cover"
                     :class="{'selected':typeof this.focusItem!=='undefined' && this.focusItem.data===folder}"
                     :src="folder.cover">
            </a-image>
          </div>
          <div class="folder-name">{{ folder.name }}</div>
          <div class="inner-content-count">{{ folder.contentsCount }} 内容</div>
        </div>
        <template #overlay>
          <a-menu>
            <a-menu-item key="openPath" @click="open(folder.baseInfo.path+folder.baseInfo.filename)">打开</a-menu-item>
            <a-menu-item key="find" @click="showItemInFolder(folder.baseInfo.path+folder.baseInfo.filename)">查看所在位置
            </a-menu-item>
            <a-menu-item key="delete" @click="trashItem(folder.baseInfo.path+folder.baseInfo.filename)">删除文件夹
            </a-menu-item>
          </a-menu>
        </template>
      </a-dropdown>
    </div>
  </div>
  <a-modal v-model:visible="askModelVisible" :closable="false" :centered="true">
    <template #footer>
<!--      <a-checkbox>全部应用</a-checkbox>-->
      <a-button  @click="this.askModelVisible=false;this.copyContent(this.askingContent)">保留两者</a-button>
      <a-button @click="this.askModelVisible=false;this.moveContent(this.askingContent)">替换</a-button>
      <a-button @click="stopMove">终止移动</a-button>
    </template>
    <p>此文件夹已经存在名称为"<strong>{{ askingContent.filename }}</strong>"的内容。您要使用正在移动的内容来替换它吗？</p>
  </a-modal>
</template>
<script>
import { mapState } from 'vuex'
import ipc from '@/utils/ipc'
import { Modal } from 'ant-design-vue'
import { CaretDownOutlined, CaretRightOutlined } from '@ant-design/icons-vue'
import pathTools from '@/utils/pathTools'
import contentModel from '@/utils/model/contentModel'
import statusReturn from '@/utils/statusReturn'

const fs = eval('require')('fs')
export default {
  name: 'SubFolderList',
  props: {
    msg: String
  },
  components: {
    CaretDownOutlined, CaretRightOutlined
  },
  data () {
    return {
      showFolderList: true,
      askModelVisible: false,
      waitingMoveList: [],//等待移动的列表
      movedContentCount: 0,//移动成功的数量
      failureContentCount: 0,//失败的数量
      askingContent: {},//询问中的内容
      targetFolder: null
    }
  },
  computed: {
    ...mapState({
      subFolders: state => state.subFolder.subFolders,
      itemSize: state => state.ui.itemSize,
      focusItem: state => state.ui.focusedItem
    })
  },
  methods: {
    onDragEnter (event, folder) {
      folder.hover = true
    },
    onDragLeave (event, folder) {
      folder.hover = false
    },
    onDragOver (event, folder) {
      if (!folder.hover) {
        folder.hover = true
      }
      event.preventDefault()
    },
    /**
     * 弹出队列下一个内容，并尝试移动
     */
    moveNextContent () {
      let folder = this.targetFolder
      //检查还有没有要移动的
      if (this.waitingMoveList.length <= 0) {
        this.$message.success(`成功移动 ${this.movedContentCount} 个文件到文件夹内`)
        folder.hover = false
        return
      }
      let nextContent = this.waitingMoveList.shift()
      console.log('要移动的对象',nextContent)
      let all = pathTools.getAllFromRelativeFullPath(nextContent.meta.relative_path_full)
      let allTarget=pathTools.getAllFromRelativeFullPath(folder.meta.relative_path_full)
      if (fs.existsSync(allTarget.storeFullPath+'/'+all.filename)) {
        //如果目标已经存在，则询问是否要覆盖
        this.askingContent = nextContent
        this.askingContent.filename = all.filename
        this.askModelVisible = true
        return
      } else {
        this.moveContent(nextContent)
      }

    },
    /**
     * 开始移动内容
     * @param list
     * @param folder
     */
    startMoveContents (list,folder) {
      this.waitingMoveList = list
      this.targetFolder=folder
      this.movedContentCount = 0
      this.failureContentCount = 0
      this.moveNextContent(folder)
    },
    stopMove () {
      this.askModelVisible = false
      this.targetFolder.hover = false
    },
    /**
     * 已经存在同名文件
     * @param content
     * @param moveNext
     */
    copyContent(content,moveNext=true){
      let allSource=pathTools.getAllFromRelativeFullPath(content.meta.relative_path_full)
      let targetPath=this.targetFolder.meta.relative_path_full+'/'+allSource.name+allSource.ext
      let testRelativePath=targetPath
      let testPath=pathTools.getStorePathFromRelativePath(testRelativePath)
      let testNo=1
      while(fs.existsSync(testPath)){
        testRelativePath=this.targetFolder.meta.relative_path_full+'/'+allSource.name+' 副本' + (testNo>1?String(testNo):'')+allSource.ext
        testPath=pathTools.getStorePathFromRelativePath(testRelativePath)
        testNo++
      }
      let result=contentModel.move(content.meta.uuid, testRelativePath, false)
      if (result.status === statusReturn.successCode) {
        this.movedContentCount=this.movedContentCount+1
        this.$store.commit('content/removeContent',content)
        this.$store.commit('ui/clearFocusItem')
      } else {
        this.failureContentCount=this.failureContentCount+1
      }
      if(moveNext)
        this.moveNextContent()
      //修改当前的内容名称为原先的名称加（1）
    },
    /**
     *
     * @param content 要移动的内容
     * @param moveNext 继续移动队列中的下一个？
     */
    moveContent (content,moveNext=true) {
      let allSource=pathTools.getAllFromRelativeFullPath(content.meta.relative_path_full)
      let result=contentModel.move(content.meta.uuid, this.targetFolder.meta.relative_path_full+'/'+allSource.filename, true)
      if (result.status === statusReturn.successCode) {
        this.movedContentCount=this.movedContentCount+1
        this.$store.commit('content/removeContent',content)
        this.$store.commit('ui/clearFocusItem')
      } else {
        this.failureContentCount=this.failureContentCount+1
      }
      if(moveNext)
        this.moveNextContent()
    },
    onDrop (event, folder) {
      this.startMoveContents(this.$store.state.ui.selectedItems,folder)
    },

    toggleShowFolderList () {
      this.showFolderList = !this.showFolderList
      console.log(this.showFolderList)
    },
    changePath (path) {
      this.$store.dispatch('ui/changeFolder', { path: this.$store.state.ui.currentFolder.path + path + '/' })
    },
    selectItem (folder) {
      this.$store.commit('ui/focusItem', {
        type: 'folder',
        data: folder
      })
    },
    open: ipc.openExternal,
    showItemInFolder: ipc.showItemInFolder,
    trashItem (path) {
      let that = this
      Modal.confirm({
        title: '删除确认',
        content: '是否确认删除文件夹？此操作会删除文件夹下面全部的内容。',
        okText: '确认',
        cancelText: '取消',
        onOk: () => {
          ipc.trashFolder(path)
          setTimeout(() => {
            that.$store.dispatch('ui/changeTab', { name: 'all' })
          }, 200)
          this.$message.success('删除文件夹成功。')
        }
      })
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.ant-modal-footer {
  text-align: left;
}
</style>
