import tools from "@/utils/tools";
import pathTools from "@/utils/pathTools";
import libDb from '@/utils/db/libDb'
import statusReturn from '@/utils/statusReturn'
const fs=eval('require')('fs')

const contentModel = {
    addLink(folder, title, url, cover,favicon, remark){
      return window.libDb.addUrl(folder, title, url, cover,favicon, remark)
    },
    getMetaFromFile(relativePath){
       relativePath=pathTools.convertToCommonRelativePath(relativePath)
       return window.libDb.getMeta(relativePath)
    },
    /**
     * 通过文件获得Content对象
     * @returns {{cover: string, baseInfo: {path, filename, modifyTime: T | T, addTime: T | T, size, createTime: T | T, sizeText: string, relativePath}, contentsCount, meta: {}, name}}
     * @param relativeFullPath
     */
    getSubFolderFromFolder(relativeFullPath) {
        let fullPath=pathTools.getFullPathFromRelativeFullPath(relativeFullPath)
        let filename=pathTools.getFilenameFromRelativeFullPath(relativeFullPath)
        let fileInfo=fs.statSync(fullPath)

        let path=pathTools.convertToCommonDir(relativeFullPath.substr(0,relativeFullPath.lastIndexOf('/')))
        let relativePath=pathTools.convertToCommonRelativeDir(path.replace(window.$defaultStrorePath,''))

        let meta=window.libDb.getMeta(pathTools.getRelativeFullPathFromFullPath(path+filename))
        let contentCount=0
        try{
            contentCount=fs.readdirSync(fullPath).length
        }catch (e) {
            console.warn(e)
            return null
        }

        let subFolder={
            name: filename,
            contentsCount: contentCount,
            cover: './img/folder.svg',
            meta:meta,
            baseInfo: {
                path: path,
                relativePath: relativePath,
                filename: filename,
                createTime: fileInfo.birthtimeMs,
                addTime: fileInfo.ctimeMs,
                modifyTime: fileInfo.mtimeMs,
                size: fileInfo.size,
                sizeText: tools.convertToText(fileInfo.size)
            },
        }

        return subFolder
    },
    removeContent(path,isFullPath=true){
        let relativePath=path
        if(isFullPath){
            relativePath=pathTools.getRelativeFullPathFromFullPath(path)
        }
        libDb.removeMeta(relativePath)
    },
    rename (uuid, inputName) {
        try {
            var contentMeta = window.libDb.getMetaFromUuid(uuid)
            if (!contentMeta) {
                return  statusReturn.failure('文件夹不存在。')
            }
            let newName=pathTools.filterFilename(inputName).filename
            let newFilename = newName
            let newPath = pathTools.getStorePathFromRelativePath(contentMeta.relative_path) + newFilename
            if (fs.existsSync(newPath)) {
                return statusReturn.failure('已存在同名内容。')
            }else{
                fs.renameSync(pathTools.getFullPathFromRelativeFullPath(contentMeta.relative_path_full),newPath)
                if(contentMeta.base_info){
                    contentMeta.base_info.update_time=Date.now()
                }else{
                    contentMeta.base_info={
                        update_time:Date.now()
                    }
                }
                contentMeta.name=newName
                contentMeta.relative_path_full=contentMeta.relative_path+newFilename
                libDb.updateMeta(uuid,{name:contentMeta.name,relative_path_full:contentMeta.relative_path_full,base_info:contentMeta.base_info})
                return statusReturn.success('修改成功。',contentMeta)
            }
        } catch (e) {
            console.warn(e)
            return statusReturn.failure('意外错误')
        }
    },
    update(uuid,saveData){
        try{
            let contentMeta=libDb.getMetaFromUuid(uuid)
            contentMeta=Object.assign(contentMeta,saveData)
            libDb.updateMeta(uuid,contentMeta)
            return statusReturn.success('修改成功。',contentMeta)
        }catch (e) {
            return statusReturn.failure('意外错误')
        }
    }
}

export default contentModel
