<template>
  <div @dblclick="openDetail(content)" @click="setSelected" class="cover-wrapper" style="position: relative" :style="{width:itemSize+'px',height:itemSize+'px'}">
    <div class="type-label">图片</div>
    <a-image :style="{width:itemSize+'px',height:itemSize+'px',objectFit:'cover'}"
             :preview="false" style="background: #f1f1f1"
             class="content-cover"
             :src="cover">
    </a-image>

    <div class=" type-label preview" @click.stop=""><zoom-in-outlined /></div>
  </div>
  <div class="folder-name" style="position: relative">{{ content.name }}</div>
  <div class="ext"><span >{{ content.ext.substr(content.ext.lastIndexOf('.')+1) }}</span> {{ content.extData.width }} X {{ content.extData.height }}</div>
</template>

<script>
import { mapState } from 'vuex'
import {ZoomInOutlined} from '@ant-design/icons-vue';
import contentTypeHelper from "@/utils/contentTypeHelper";
export default {
  name: 'TypeImg',
  emits:['setSelected','openDetail'],
  components:{
    ZoomInOutlined
  },
  props: {
    content: Object
  },
  data(){
    return {
      contentTypeHelper
    }
  },
  computed: {
    ...mapState(
      {
        itemSize: state => state.ui.itemSize
      },

    ),
    cover(){
      if(this.content.cover.startsWith('/')){
        return 'file://'+this.$store.state.config.storePath+this.content.cover
      }else{
        return this.content.cover
      }
    }
  },
  methods:{
    openDetail(content){
      this.$emit('openDetail',content)
    },
    setSelected(){
      this.$emit('setSelected')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

